import React, { useEffect, useState } from 'react';
import s from './status.module.css'



const ProfileStatusHook = (props) => {
    /* console.log(props.Profile!= null ? props.Profile.userId + " –¢–µ–∫—É—â–∏–π —Å—Ç–∞—Ç—É—Å": null)
    console.log(props.id!= null ? props.id + " –ú–æ–π Id" : null) */
    let id = (props.id!= null ? props.id : 0);
    let userId = (props.Profile!= null ? props.Profile.userId : 1)
    
    let [editMod, setEditMod] = useState(false);
    let [status, setStatus] = useState(props.Status);
    useEffect (()=> {
      
        setStatus(props.Status);
    }, [props.Status] );

    const activEditMod = () => {
        setEditMod(true)
    }
    const DiActivEditMod = () => {
        setEditMod(false)
        props.updateStatus(status)
    }
    const onStatusChange = (e) => {
        setStatus(e.currentTarget.value)
    }
    let ssws = 10;

    return <div>
        <div className={s.status}>
            {editMod
                ? <input  onChange={onStatusChange} autoFocus={true} onBlur={() => DiActivEditMod()} value={status} />
                : <div  onClick={id!= userId ? null : () => activEditMod()} >{props.Status ? props.Status : '—É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å —Å—Ç–∞—Ç—É—Å'}</div>
            }
        </div>


    </div>

}

export default ProfileStatusHook;