import s from './ProfileContactsForm.module.css'
import { Form, Field } from 'react-final-form'
import React from 'react'



const MyForm = ({ Profile, setEditMod,saveProfile }) => {
debugger
    let onSubmit = (formData) => {
        saveProfile(formData)
        setEditMod(false)
        
        
    }
    return (
        <Form
            onSubmit={onSubmit}
            render={({ handleSubmit, form, submitting, pristine, values }) => (
                <form onSubmit={handleSubmit}>
                    <div className={s.ProfileContacts__button}>
                        <button type="submit" >–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
                    </div>
                    <div className={s.userId}>
                        <span className={s.ProfileContacts__span}>userId</span>
                        <Field 
                            name="userId"
                            component="input"
                            type="text"
                            defaultValue={Profile.userId}
                            
                        />
                    </div>
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>–ò–º—è</span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="fullName"
                            component="input"
                            type="text"
                            defaultValue={Profile.fullName}
                            
                        />
                    </div>
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>–û —Å–µ–±–µ</span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="AboutMe"
                            component="input"
                            type="text"
                            defaultValue={Profile.aboutMe}
                            
                        />
                    </div>
                   
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>–ò—â—É —Ä–∞–±–æ—Ç—É?</span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="lookingForAJob"
                            component="input"
                            type="checkbox"                      
                        />
                    </div>
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>–ú–æ–∏ —Å–∫–∏–ª—ã</span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="lookingForAJobDescription"
                            component="input"
                            type="text"
                            defaultValue={Profile.lookingForAJobDescription}
                            
                        />
                    </div>
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>github </span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="contacts.github"
                            component="input"
                            type="text"
                            placeholder="https://"
                            defaultValue={Profile.contacts.github}
                            
                        />
                    </div>
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>vk </span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="contacts.vc"
                            component="input"
                            type="text"
                            placeholder="https://"
                            defaultValue={Profile.contacts.vc}
                        />
                    </div>
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>facebook </span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="contacts.facebook"
                            component="input"
                            type="text"
                            placeholder="https://"
                            defaultValue={Profile.contacts.facebook}
                        />
                    </div >
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>instagram </span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="contacts.instagram"
                            component="input"
                            type="text"
                            placeholder="https://"
                            defaultValue={Profile.contacts.instagram}
                        />
                    </div>
                    
                    
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>twitter </span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="contacts.twitter"
                            component="input"
                            type="text"
                            placeholder="https://"
                            defaultValue={Profile.contacts.twitter}
                        />
                    </div>
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>website </span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="contacts.website"
                            component="input"
                            type="text"
                            placeholder="https://"
                            defaultValue={Profile.contacts.website}
                        />
                    </div>
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>youtube </span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="contacts.youtube"
                            component="input"
                            type="text"
                            placeholder="https://"
                            defaultValue={Profile.contacts.youtube}
                        />
                    </div>
                    <div className={s.ProfileContacts}>
                        <span className={s.ProfileContacts__span}>mainLink </span>
                        <Field
                            className={s.ProfileContacts__form}
                            name="contacts.mainLink"
                            component="input"
                            type="text"
                            placeholder="https://"
                            defaultValue={Profile.contacts.mainLink}
                            
                        />
                    </div>
                    
                    
                    


                </form>
            )}
        />

    )
}

const Contact = ({contactTitle, contactValue}) => {
    return <div><d>{contactTitle}: {contactValue}</d></div>
}


export default MyForm;