import Profile from "./Profile";
import React, { useState, useEffect } from 'react';
import { connect } from "react-redux";
import { getUsersProfile, getStatus, updateStatus, savePhoto, saveProfile } from '../../Redux/Profile-reduser'
import { withRouter } from "react-router";
import { compose } from "redux";
import { withAuthRedirect } from "../../hoc/withAuthRedirect";
import { getUsersSelect } from "../../Redux/users-selectors";
import { followFrends, unfollowFrends,} from "../../Redux/Users-reduser";


const ProfileContainer = (props) => {
    
    
    let userId = props.match.params.userId;
    
    if (!userId) {
        userId = 16358;
    }


    useEffect(() => {
        props.getUsersProfile(userId);
        props.getStatus(userId);
    }, [userId]);

    return <>

        <Profile  {...props} followFrends={props.followFrends} unfollowFrends={props.unfollowFrends} users={props.users} id={props.id} Profile={props.Profile} Status={props.Status} updateStatus={props.updateStatus} savePhoto={props.savePhoto} saveProfile={props.saveProfile}/>
    </>

}

let mapStateToProps = (state) => {
    return {
        Profile: state.profilePage.Profile,
        Status: state.profilePage.Status,
        id: state.auth.userId,
        users:getUsersSelect(state),

    }
}

export default compose(
    withRouter,
    connect(mapStateToProps, { getUsersProfile, getStatus, updateStatus, savePhoto, saveProfile, followFrends,  unfollowFrends }),
    withAuthRedirect,
)(ProfileContainer);




