import { Post } from './Post/Post'
import s from './Posts.module.css'
import React, { useState , useEffect } from 'react';
import { Form, Field } from 'react-final-form'


export const Posts = (props) => {
    let state = props.profilePage;
    let postElenents = state.post.map(p => <Post id={p.id} message={p.message} like={p.like} />)

    return (
        <div className={s.Posts}>
            <MyForm sendPostCreator={props.sendPostCreator} value={props.value}/>
            <div className={s.Posts__content}>{postElenents}</div>  
        </div>
    )
}


const MyForm = (props) => {
    let onSubmit = (textMessage) => {
        props.sendPostCreator(textMessage.message) 
       
     }
     
    return (
        <Form
            onSubmit={onSubmit} 
            render={({ handleSubmit,form, submitting, pristine,}) => (
                <form onSubmit={handleSubmit} >
                    <div>
                        <Field className={s.texterea}
                            name="message"
                            component="textarea"
                            type="text"
                            placeholder="–ß—Ç–æ —É –≤–∞—Å –Ω–æ–≤–æ–≥–æ?"
                            defaultValue={form.mutators.clear}
                            
                        />
                    </div>
                    
                    <div className={s.buttons}>
                        <button  type="submit" disabled={submitting || pristine}>–û–ø—É–±–ª–∏–∫–æ–≤–∞—Ç—å</button>
                    </div>
                </form>
            )}
        />

    )
}





export default Posts;