
import s from './Users.module.css'
import userPhoto from '../../img/images.png'
import { NavLink } from 'react-router-dom';
import { useState } from 'react';




let Users = (props) => {
    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }
    let portionCount = Math.ceil(pagesCount / props.pageSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * props.pageSize + 1;
    let rightPortionPageNumber = portionNumber * props.pageSize;


    return (
        <div className={s.Users}>
            <div className={s.totalUsersCount}>
                {props.totalUsersCount} –í—Å–µ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π
            </div>
            <div>
                <div className={s.UsersPage}>
                    {portionNumber &&
                        <button className={s.portionNumber} disabled={leftPortionPageNumber <= 1 ? true : false} onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button>}
                    {pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber).
                        map((p) => {
                            return <span className={props.currentPage === p && s.activPage} key={p} onClick={() => { props.onPageChanged(p) }}>{p}</span>
                        })}
                    {portionCount > portionNumber && <button className={s.portionNumber} onClick={() => { setPortionNumber(portionNumber + 1) }}>NEXT</button>}
                </div>

            </div>
            
            {props.users.map(u =>
                <div className={s.UserData}>
                    <div className={s.Userimg}>
                        <NavLink to={'./Profile/' + u.id}>
                            <img src={u.photos.small != null ? u.photos.small : userPhoto} alt="" />
                        </NavLink>
                    </div>
                    <div>
                    <div className={s.UserName}>{u.name}</div>
                        <div className={s.UserStatus}>{u.status || "---"}</div>
                        <div className={s.divButton}>
                            {u.followed
                                ? <button className={s.Button} onClick={() => { props.unfollow(u.id) }}>–£–¥–∞–ª–∏—Ç—å –∏–∑ –¥—Ä—É–∑–µ–π</button>
                                : <button className={s.Button} onClick={() => { props.follow(u.id) }}>–î–æ–±–∞–≤–∏—Ç—å –≤ –¥—Ä—É–∑—å—è</button>}
                        </div>
                    </div>
                </div>)}
        </div>
    )
}

export default Users;