const UPDATE_NEW_MESSAGE_BODY = "UPDATE_NEW_MESSAGE_BODY";
const SEND_MESSAGE = "SEND_MESSAGE";

let initialState = {

    message: [
        { Id: 1, message: "hey", like: 2, },

    ],

};


const MessageReduser = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE: {
            let newMessage = { Id: 2, message: action.newMessage, like: 0, };
            return {...state,message: [...state.message, newMessage],}

        }
        default:
            return state;
    }


}
export const sendMessageCreator = (newMessage) => ({ type: SEND_MESSAGE, newMessage })






export default MessageReduser;