import s from './ProfileFrens.module.css'
import userPhoto from '../../../img/images.png'
import { NavLink } from 'react-router-dom';
import React, { useState ,  useEffect  } from 'react';



export const ProfileFrens = (props) => {

    return (

        <div className={s.ProfileFrens}>
            <div>–î—Ä—É–∑—å—è {props.users.length}</div>
        <div className={s.ProfileFrens__data}>
        {props.users.map(u =>
                <div className={s.Frends}>
                    <div className={s.Userimg}>
                        <NavLink  to={'/Profile/' + u.id}>
                            <img  src={u.photos.small != null ? u.photos.small : userPhoto} alt="" />
                        </NavLink>
                    </div>
                    <div>{u.name}</div>
                    
                   
                </div>)}
        </div>
        </div>
    )
}
