import Preloader from '../../comon/Preloader'

import s from './ProfileImg.module.css'
import userPhoto from '../../../img/images.png'

export const ProfileImg = (props) => {
   
    if (!props.Profile) {
        return <Preloader />
    }
    return (
        <div className={s.ProfileImg}>
            {<img src={props.Profile.photos.large != null ? props.Profile.photos.large : userPhoto} alt="" />}
        </div>
    )
}
