import React, { useEffect, useState ,getState } from 'react';
import Preloader from '../../comon/Preloader';
import ProfileContacts from '../ProfileContacts/ProfileContacts';
import ProfileContactsForm from '../ProfileContactsForm/ProfileContactsForm';
import ProfileStatusHook from '../ProfileStatus/ProfileStatusHook'
import s from './ProfileConect.module.css'


const ProfileConect = (props) => {
    let [editMod, setEditMod] = useState(false);

    return <div className={s.ProfileConect}>
        
        <div className={s.ProfileConect__name}>{props.Profile != null ? props.Profile.fullName  : "–ü–æ–¥–≥—Ä—É–∂–∞–µ–º"}</div>
        <ProfileStatusHook Status={props.Status} updateStatus={props.updateStatus} id={props.id} Profile={props.Profile} />
        {editMod ? <ProfileContactsForm Profile={props.Profile } saveProfile={props.saveProfile} setEditMod={setEditMod}/> : <ProfileContacts Profile={props.Profile } id={props.id} setEditMod={setEditMod}/>}
    </div>

}

export default ProfileConect;