
import { Redirect } from 'react-router'
import PostsConteiner from './Posts/PostsConteiner'
import s from './Profile.module.css'
import  ProfileFrensContainet  from './ProfileFrens/ProfileFrensContainet'
import { ProfileImg } from './ProfileImg/ProfileImg'

import ProfileConect from './ProfileConect/ProfileConect'
import ProfileButton from './ProfileButton/ProfileButton'


const Profile = (props) => {
    return (

        <div className={s.Profile}>
            <div className={s.Profile__mein}>
                <ProfileImg Profile={props.Profile} />
                <ProfileButton savePhoto={props.savePhoto} Profile={props.Profile} users={props.users}  followFrends={props.followFrends} unfollowFrends={props.unfollowFrends}/>
                <ProfileFrensContainet />
            </div>
            <div className={s.Profile__info}>
                <ProfileConect Status={props.Status} updateStatus={props.updateStatus} Profile={props.Profile} saveProfile={props.saveProfile} id={props.id}  />

                <PostsConteiner />
            </div>



        </div>
    )
}

export default Profile;