import { connect } from "react-redux";
import Users from "./Users";
import React from 'react'
import { follow, unfollow, setCurrentPage,  getUsers } from "../../Redux/Users-reduser";
import { getUsersSelect, getTotalUsersCountSelect, getPageSizeSelect, getCurrentPageSelect, getIsFetchingSelect } from "../../Redux/users-selectors";
import { withAuthRedirect } from '../../hoc/withAuthRedirect';
import { compose } from 'redux';




class UsersComp extends React.Component {
    componentDidMount() {
        this.props.getUsers(this.props.currentPage, this.props.pageSize);
    }
    onPageChanged = (pageNumber) => {
        this.props.getUsers(pageNumber, this.props.pageSize); 
    }


    render() {
        return (
            <>
                {this.props.isFetching ? "–ó–∞–≥—Ä—É–∑–∫–∞..." : null}
                <Users totalUsersCount={this.props.totalUsersCount}
                    pageSize={this.props.pageSize}
                    currentPage={this.props.currentPage}
                    onPageChanged={this.onPageChanged}
                    users={this.props.users}
                    follow={this.props.follow}
                    unfollow={this.props.unfollow}
                />
            </>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        users:getUsersSelect(state),
        totalUsersCount: getTotalUsersCountSelect(state),
        pageSize: getPageSizeSelect(state),
        currentPage: getCurrentPageSelect(state),
        isFetching: getIsFetchingSelect(state),
    }
}

const UsersContainer = compose(connect(mapStateToProps, { follow, unfollow,   setCurrentPage,  getUsers }), withAuthRedirect)(UsersComp);
export default UsersContainer;


