import s from './Post.module.css'
import React, { useState } from 'react'

export const Post = (props) => {
    let [likeCount, setLikeCount] = useState(false);
    let like = (e) => {
        if (e === true) {
            setLikeCount(true)
        } else {
            setLikeCount(false)
        }
    }


    return (
        <div className={s.Post}>
            <div className={s.Post__message}>
                    {props.message}      
            </div>
            <button onClick={likeCount ? () => { like(false) } : () => { like(true) }}>{likeCount ? "1" : "0"} Like</button>
        </div>
    )
}
