import { usersAPI,profileAPI } from "../api/api";

const FOLLOW = "FOLLOW";
const UNFOLLOW = " UNFOLLOW";
const SET_USERS = "SET_USERS";
const TOTAL_USERS_COUNT = "TOTAL_USERS_COUNT";
const SET_CURRENT_PAGE = "SET_CURRENT_PAGE";
const SET_FETCHING = "SET_FETCHING";

let initialState = {

    users: [],
    pageSize: 10,
    totalUsersCount: 20,
    currentPage: 1,
    isFetching: false,


};

const UserReduser = (state = initialState, action) => {

    switch (action.type) {

        case FOLLOW: {
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return { ...u, followed: true }
                    }
                    return u;
                })
            }

        }
        case UNFOLLOW: {
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return { ...u, followed: false }
                    }
                    return u;
                })
            }
        }
        case SET_USERS:
            return {
                ...state, users: [...state.users],
                users: action.users,

            }
        case TOTAL_USERS_COUNT:

            return {
                ...state, totalUsersCount: action.usersCount,

            }
        case SET_CURRENT_PAGE:

            return {
                ...state, currentPage: action.pageNumber,

            }
        case SET_FETCHING:

            return {
                ...state, isFetching: action.isFetching,

            }


        default:
            return state;
    }





}

export const followSuccess = (userId) => ({ type: FOLLOW, userId })
export const unfollowSuccess = (userId) => ({ type: UNFOLLOW, userId })
export const serUsers = (users) => ({ type: SET_USERS, users })
export const totalCount = (usersCount) => ({ type: TOTAL_USERS_COUNT, usersCount })
export const setCurrentPage = (pageNumber) => ({ type: SET_CURRENT_PAGE, pageNumber })
export const setFetching = (isFetching) => ({ type: SET_FETCHING, isFetching })



export const getUsers = (currentPage, pageSize) => {
    return (dispatch) => {
        dispatch(setFetching(true));
        dispatch(setCurrentPage(currentPage));
        usersAPI.getUsers(currentPage, pageSize).then(data => {
            dispatch(serUsers(data.items));
            dispatch(totalCount(data.totalCount));
            dispatch(setFetching(false))
            
        });
    }
}
export const follow = (userId) => {
    return (dispatch) => {
        usersAPI.follow(userId)
            .then(response => {
                if (response.data.resultCode === 0) {
                    dispatch(followSuccess(userId))
                }
            });
    }
}
export const unfollow = (userId) => {
    return (dispatch) => {
        usersAPI.unfollow(userId)
            .then(response => {
                if (response.data.resultCode === 0) {
                    dispatch(unfollowSuccess(userId))
                }
            });
    }
}

export const followFrends = (userId) => {
    return (dispatch) => {
        usersAPI.follow(userId)
            .then(response => {
                if (response.data.resultCode === 0) {
                    dispatch(followSuccess(userId))
                    dispatch(getFriendAC())
                }
            });
    }
}
export const unfollowFrends = (userId) => {
    return (dispatch) => {
        usersAPI.unfollow(userId)
            .then(response => {
                if (response.data.resultCode === 0) {
                    dispatch(unfollowSuccess(userId))
                    dispatch(getFriendAC())
                }
            });
    }
}





export const getFriendAC = () => {
    return (dispatch) => {
        profileAPI.getFriend()
            .then(response => {
                dispatch(serUsers(response.data.items))
            });
    }
}





export default UserReduser;