import React from 'react'
import s from './login.module.css'
import { Form, Field } from 'react-final-form'
import { connect } from 'react-redux'
import { Redirect } from 'react-router'
import {auth, login} from '../../Redux/auth-reduser'





const Login = (props) => {

    const onSubmit = (fields) => {
        props.login(fields.emeil, fields.password, fields.rememberMe);
       
    };
    if (props.isAuth) {
        return <Redirect to={"/profile"}/>
    };
    return (
        <Form
            onSubmit={onSubmit}
            render={({ handleSubmit, form, submitting, pristine, values }) => (

                <form onSubmit={handleSubmit}>

                    <div>
                        <div>testreact1@mail.ru</div>
                        <Field className={s.input}
                            name="emeil"
                            component="input"
                            type="text"
                            placeholder="emeil"
                            defaultValue="testreact1@mail.ru"
                        />
                    </div>
                    <div>
                        <div>pass2021</div>
                        <Field className={s.input}
                            name="password"
                            component="input"
                            type="text"
                            placeholder="password"
                            defaultValue="pass2021"
                        />
                    </div>
                    <div>
                        <label>rememberMe</label>
                        <Field
                            name="rememberMe"
                            component="input"
                            type="checkbox"
                           
                            
                        />
                    </div>
                    
                    <div className={s.buttons}>
                       <div> <button type="submit" disabled={submitting || pristine}>Submit</button></div>
                        <div><button
                            type="button"
                            onClick={form.reset}
                            disabled={submitting || pristine}
                        >Reset</button>
                        </div>
                    </div>
                  
                </form>
            )}
        />

    )
}
const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})



export default connect(mapStateToProps,{login})(Login);