import React from 'react';
import s from './ProfileButton.module.css'



const ProfileButton = (props) => {


  let fileRef = React.createRef();
  const triggerClick = () => {
    fileRef.current.click()

  }
  const savePhoto = (e) => {
    if (e.target.files.length) {
      props.savePhoto(e.target.files[0]);
    }

  }

  if (!props.Profile) {
    return (
      <div>
        –ü–æ–¥–≥—Ä—É–∂–∞–µ–º
      </div>
    )

  } if (props.Profile.userId === 16358) {
    return (
      <div className={s.divButton}>
        <input onChange={savePhoto} className={s.butt} type="file" ref={fileRef} />
        <button className={s.Button} onClick={triggerClick}>–û–±–Ω–æ–≤–∏—Ç—å —Ñ–æ—Ç–æ</button>
      </div>
    )
  } if (props.Profile.userId != 16358) {
    let userProfile = props.users.filter(item => item.id === props.Profile.userId)
    debugger
    return (
      <div className={s.divButton}>
        {userProfile.length === 1
          ? <button className={s.Button} onClick={() => { props.unfollowFrends(props.Profile.userId) }}>–£–¥–∞–ª–∏—Ç—å –∏–∑ –¥—Ä—É–∑–µ–π</button>
          : <button className={s.Button} onClick={() => { props.followFrends(props.Profile.userId)  }}>–î–æ–±–∞–≤–∏—Ç—å –≤ –¥—Ä—É–∑—å—è</button>}
      </div>
    )
  }



}

export default ProfileButton;