import s from './Message.module.css'
import { MessageText } from './MessageText/MessageText'
import { Form, Field } from 'react-final-form'
import React from 'react'





export const Message = (props) => {
    

    let state = props.dialogPage;

    let messagesElenents = state.message.map(m => <MessageText id={m.id} message={m.message} like={m.like} />)

    
    
    return (
        <div className={s.Message}>
            <div className={s.User}>
                Durov
        </div>
            <div className={s.Dial–æg}>
                {messagesElenents}
                < MyForm sendMessageCreator={props.sendMessageCreator}/>
            </div>
        </div>
    )
}


const MyForm = (props) => {

    let onSubmit = (textMessage) => {
        props.sendMessageCreator(textMessage.message)
        
     }
    return (
        <Form
            onSubmit={onSubmit}
            render={({ handleSubmit, form, submitting, pristine, values }) => (
                <form onSubmit={handleSubmit}>
                    <div>
                        <Field
                            name="message"
                            component="textarea"
                            type="text"
                            placeholder="Message"
                        />
                    </div>
                    
                    <div className="buttons">
                        <button type="submit" disabled={submitting || pristine}>–û—Ç–ø—Ä–∞–≤–∏—Ç—å</button>
                    </div>
                </form>
            )}
        />

    )
}

export default Message;