import { usersAPI , profileAPI } from "../api/api";
const UPDATE_NEW_POST_BODY = "UPDATE_NEW_POST_BODY";
const SEND_POST = "SEND_POST";
const SEND_USERS_PROFILE = "SEND_USERS_PROFILE";
const SEND_STATUS = "SEND_STATUS";
const SAVE_PHOTO = "SAVE_PHOTO";

let initialState = {

    post: [
        { Id: 1, message: "Hey", like: 0, },
        
    ],
    Profile: null,
    Status: '',

   
};

const ProfileReduser = (state = initialState, action) => {
    
    switch (action.type) {
        case SEND_POST: {
            let newPost = { Id: 3, message: action.textPost, like: 0, };
            return {
                ...state,
                post: [...state.post, newPost],
            }
            
        }
        case SEND_USERS_PROFILE: {
            return {
                ...state,
                Profile: action.Profile
            }
        }
        case SEND_STATUS: {
            return {
                ...state,
                Status: action.status
            }
        }
            
        
        case SAVE_PHOTO: {
            return {
                ...state,Profile: {...state.Profile, photos: action.photos}
            }
        }
        
            
         
           
        
        default:
            return state;
    }





}

export const sendPostCreator = (textPost) => ({ type: SEND_POST, textPost })


const serUsersProfile = (Profile) => ({ type: SEND_USERS_PROFILE, Profile })
export const getUsersProfile = (userId) => {
    return (dispatch) => {
        usersAPI.getProfile(userId)
        .then(response => {
               dispatch(serUsersProfile(response.data));
            });
    }
}
export const setStatus = (status) => ({ type: SEND_STATUS, status })

export const getStatus = (userId) => {
    return (dispatch) => {
        profileAPI.getStatus(userId)
        .then(response => {
               dispatch(setStatus(response.data));
                
            });
    }
}
export const updateStatus = (status) => {
  
    return (dispatch) => {
        profileAPI.updateStatus(status)
        .then(response => {
               if(response.data.resultCode === 0){
                
                dispatch(setStatus(status));
               }
                
            });
    }
}
const savePhotoAC = (photos) => ({type:SAVE_PHOTO, photos})
export const savePhoto = (foto) => {
    return (dispatch) => {
        profileAPI.savePhoto(foto)
        .then(response => {
               if(response.data.resultCode === 0){      
                dispatch(savePhotoAC(response.data.data.photos));
               }
                
            });
    }
}

export const saveProfile = (formData) => {
    return (dispatch) => {
        profileAPI.saveProfile(formData)
        .then(response => {
               if(response.data.resultCode === 0){      
                dispatch(getUsersProfile(formData.userId));
               }
                
            });
    }
}





export default ProfileReduser;