import { connect } from "react-redux";
import { getUsersSelect, getPageSizeSelect, getCurrentPageSelect } from "../../../Redux/users-selectors";
import React from 'react'
import { ProfileFrens } from "./ProfileFrens";
import { getUsers, follow, unfollow, getFriendAC } from "../../../Redux/Users-reduser";





class ProfileFrensC extends React.Component {
    componentDidMount() {
        this.props.getFriendAC();
        
    }
   

    render() {
        return (
            <>
                <ProfileFrens users={this.props.users} follow={this.props.follow}
                    unfollow={this.props.unfollow} />
            </>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        users:getUsersSelect(state),
        pageSize: getPageSizeSelect(state),
        currentPage: getCurrentPageSelect(state)
    }
}

const ProfileFrensContainet = connect(mapStateToProps,{ getUsers, follow, unfollow, getFriendAC,})(ProfileFrensC);
export default ProfileFrensContainet;
