import axios from "axios";

const instanse = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {"API-KEY":"c170e51c-3d16-4186-a21b-f04d6268e3a5"}
});

export const usersAPI = {
    getUsers(currentPage, pageSize) {
        return instanse.get(`users?page=${currentPage}&count=${pageSize}`)
        .then(response=>{
            return response.data
        })
    },
    follow(userId) {
        return instanse.post(`follow/${userId}`)
      
    },
    unfollow(userId) {
        return instanse.delete(`follow/${userId}`)
    },
    authorization() {
        return instanse.get(`auth/me `)
    },
    getProfile(userId) {
        return  profileAPI.getProfile(userId)
    },

    login(email, password, rememberMe = false) {
        
        return instanse.post(`auth/login`,{ email, password, rememberMe })
    },
    logoun() {
        return instanse.delete(`auth/login`)
    }
   

}

export const profileAPI = {
    
    getProfile(userId) {
        return  instanse.get(`profile/ ` + userId )
    },
    getStatus(userId){
        return instanse.get(`profile/status/` + userId)
    },
    updateStatus(textStatus){
        return instanse.put(`profile/status/`, {status:textStatus})
    },
    getFriend(){
        return instanse.get(`users?friend=${true}`)
    },
    savePhoto(foto){
        const formData = new FormData();
        formData.append("image", foto)
        return instanse.put(`profile/photo`, formData , {headers:{
            "Content-Type": "multipart/form-data"
        }})
    },
    saveProfile(formFata){
        debugger
        return instanse.put(`profile/`, formFata)
    }

}


export const usersTestAPI = {
    getUsers(count,page) {
        return instanse.get(`users?count=${count}&page=${page}`)
    }

}
